// src/services/indexedDBCache.js - IndexedDB    

import { logger } from "../utils/logger";

const DB_NAME = 'EconomyClassCache';
const DB_VERSION = 1;
const STORE_NAME = 'dataCache';

class IndexedDBCacheService {
  constructor() {
    this.db = null;
    this.isSupported = this.checkSupport();
    this.initPromise = null;
  }

  checkSupport() {
    return typeof indexedDB !== 'undefined';
  }

  async init() {
    if (!this.isSupported) {
      logger.warn('[IndexedDBCache] IndexedDB   .');
      return null;
    }

    if (this.initPromise) {
      return this.initPromise;
    }

    this.initPromise = new Promise((resolve, reject) => {
      const request = indexedDB.open(DB_NAME, DB_VERSION);

      request.onerror = () => {
        logger.error('[IndexedDBCache] DB  :', request.error);
        reject(request.error);
      };

      request.onsuccess = () => {
        this.db = request.result;
        logger.log('[IndexedDBCache] DB  ');
        resolve(this.db);
      };

      request.onupgradeneeded = (event) => {
        const db = event.target.result;

        //  store  
        if (db.objectStoreNames.contains(STORE_NAME)) {
          db.deleteObjectStore(STORE_NAME);
        }

        //  object store 
        const objectStore = db.createObjectStore(STORE_NAME, { keyPath: 'key' });
        objectStore.createIndex('expiry', 'expiry', { unique: false });

        logger.log('[IndexedDBCache] Object Store  ');
      };
    });

    return this.initPromise;
  }

  async set(key, data, ttlSeconds = 2700) { //  []  45 (Firestore  )
    if (!this.isSupported) return false;

    try {
      await this.init();

      const transaction = this.db.transaction([STORE_NAME], 'readwrite');
      const store = transaction.objectStore(STORE_NAME);

      const cacheItem = {
        key,
        data,
        expiry: Date.now() + (ttlSeconds * 1000),
        createdAt: Date.now()
      };

      await new Promise((resolve, reject) => {
        const request = store.put(cacheItem);
        request.onsuccess = () => resolve();
        request.onerror = () => reject(request.error);
      });

      return true;
    } catch (error) {
      logger.error('[IndexedDBCache]  :', error);
      return false;
    }
  }

  async get(key) {
    if (!this.isSupported) return null;

    try {
      await this.init();

      const transaction = this.db.transaction([STORE_NAME], 'readonly');
      const store = transaction.objectStore(STORE_NAME);

      const cacheItem = await new Promise((resolve, reject) => {
        const request = store.get(key);
        request.onsuccess = () => resolve(request.result);
        request.onerror = () => reject(request.error);
      });

      if (!cacheItem) {
        return null;
      }

      // TTL 
      if (Date.now() > cacheItem.expiry) {
        //   
        await this.delete(key);
        return null;
      }

      return cacheItem.data;
    } catch (error) {
      logger.error('[IndexedDBCache]  :', error);
      return null;
    }
  }

  async delete(key) {
    if (!this.isSupported) return false;

    try {
      await this.init();

      const transaction = this.db.transaction([STORE_NAME], 'readwrite');
      const store = transaction.objectStore(STORE_NAME);

      await new Promise((resolve, reject) => {
        const request = store.delete(key);
        request.onsuccess = () => resolve();
        request.onerror = () => reject(request.error);
      });

      return true;
    } catch (error) {
      logger.error('[IndexedDBCache]  :', error);
      return false;
    }
  }

  async clear() {
    if (!this.isSupported) return false;

    try {
      await this.init();

      const transaction = this.db.transaction([STORE_NAME], 'readwrite');
      const store = transaction.objectStore(STORE_NAME);

      await new Promise((resolve, reject) => {
        const request = store.clear();
        request.onsuccess = () => resolve();
        request.onerror = () => reject(request.error);
      });

      logger.log('[IndexedDBCache]    ');
      return true;
    } catch (error) {
      logger.error('[IndexedDBCache]   :', error);
      return false;
    }
  }

  async cleanupExpired() {
    if (!this.isSupported) return 0;

    try {
      await this.init();

      const transaction = this.db.transaction([STORE_NAME], 'readwrite');
      const store = transaction.objectStore(STORE_NAME);
      const index = store.index('expiry');

      const now = Date.now();
      let deletedCount = 0;

      const cursorRequest = index.openCursor();

      await new Promise((resolve, reject) => {
        cursorRequest.onsuccess = (event) => {
          const cursor = event.target.result;
          if (cursor) {
            if (cursor.value.expiry < now) {
              cursor.delete();
              deletedCount++;
            }
            cursor.continue();
          } else {
            resolve();
          }
        };
        cursorRequest.onerror = () => reject(cursorRequest.error);
      });

      if (deletedCount > 0) {
        logger.log(`[IndexedDBCache]   ${deletedCount}  `);
      }

      return deletedCount;
    } catch (error) {
      logger.error('[IndexedDBCache]    :', error);
      return 0;
    }
  }

  async getSize() {
    if (!this.isSupported) return 0;

    try {
      await this.init();

      const transaction = this.db.transaction([STORE_NAME], 'readonly');
      const store = transaction.objectStore(STORE_NAME);

      const count = await new Promise((resolve, reject) => {
        const request = store.count();
        request.onsuccess = () => resolve(request.result);
        request.onerror = () => reject(request.error);
      });

      return count;
    } catch (error) {
      logger.error('[IndexedDBCache]   :', error);
      return 0;
    }
  }
}

//   
const indexedDBCache = new IndexedDBCacheService();

//  (  )
indexedDBCache.init().catch(err => {
  logger.error('[IndexedDBCache]  :', err);
});

//     (10)
setInterval(() => {
  indexedDBCache.cleanupExpired();
}, 10 * 60 * 1000);

export default indexedDBCache;
