// src/serviceWorkerRegistration.js
// [ ] Service Worker  

import { logger } from "./utils/logger";

const isLocalhost = Boolean(
  window.location.hostname === 'localhost' ||
  window.location.hostname === '[::1]' ||
  window.location.hostname.match(/^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/)
);

export function register(config) {
  if ('serviceWorker' in navigator) {
    //   localhost 
    const publicUrl = new URL(process.env.PUBLIC_URL, window.location.href);
    if (publicUrl.origin !== window.location.origin) {
      // PUBLIC_URL  origin    
      return;
    }

    window.addEventListener('load', () => {
      const swUrl = `${process.env.PUBLIC_URL}/service-worker.js`;

      if (isLocalhost) {
        // localhost   
        checkValidServiceWorker(swUrl, config);
        logger.log('[SW Registration] Running on localhost, service worker will be validated');
      } else {
        //     
        registerValidSW(swUrl, config);
      }
    });
  } else {
    logger.log('[SW Registration] Service workers are not supported');
  }
}

function registerValidSW(swUrl, config) {
  navigator.serviceWorker
    .register(swUrl)
    .then((registration) => {
      logger.log('[SW Registration] Service Worker registered:', registration.scope);

      //  
      registration.onupdatefound = () => {
        const installingWorker = registration.installing;
        if (installingWorker == null) {
          return;
        }

        installingWorker.onstatechange = () => {
          if (installingWorker.state === 'installed') {
            if (navigator.serviceWorker.controller) {
              //    
              logger.log('[SW Registration] New content available; please refresh');

              //   
              if (config && config.onUpdate) {
                config.onUpdate(registration);
              }
            } else {
              //   
              logger.log('[SW Registration] Content is cached for offline use');

              //   
              if (config && config.onSuccess) {
                config.onSuccess(registration);
              }
            }
          }
        };
      };
    })
    .catch((error) => {
      logger.error('[SW Registration] Error during registration:', error);
    });
}

function checkValidServiceWorker(swUrl, config) {
  //    
  fetch(swUrl, {
    headers: { 'Service-Worker': 'script' },
  })
    .then((response) => {
      const contentType = response.headers.get('content-type');
      if (
        response.status === 404 ||
        (contentType != null && contentType.indexOf('javascript') === -1)
      ) {
        //      -   
        navigator.serviceWorker.ready.then((registration) => {
          registration.unregister().then(() => {
            window.location.reload();
          });
        });
      } else {
        //    -  
        registerValidSW(swUrl, config);
      }
    })
    .catch(() => {
      logger.log('[SW Registration] No internet connection. App running in offline mode.');
    });
}

export function unregister() {
  if ('serviceWorker' in navigator) {
    navigator.serviceWorker.ready
      .then((registration) => {
        registration.unregister();
        logger.log('[SW Registration] Service Worker unregistered');
      })
      .catch((error) => {
        logger.error('[SW Registration] Unregister error:', error);
      });
  }
}

//   
export function clearCache() {
  return new Promise((resolve, reject) => {
    if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
      const messageChannel = new MessageChannel();
      messageChannel.port1.onmessage = (event) => {
        if (event.data.success) {
          resolve();
        } else {
          reject(new Error('Cache clear failed'));
        }
      };
      navigator.serviceWorker.controller.postMessage('clearCache', [messageChannel.port2]);
    } else {
      //      
      if ('caches' in window) {
        caches.keys().then((names) => {
          Promise.all(names.map((name) => caches.delete(name))).then(() => resolve());
        });
      } else {
        resolve();
      }
    }
  });
}

//    
export function skipWaiting() {
  if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
    navigator.serviceWorker.controller.postMessage('skipWaiting');
  }
}
