// src/services/globalCacheService.js -    Firestore   (IndexedDB )

import { db } from '../firebase';
import {
  doc,
  getDoc,
  getDocs,
  query,
  where,
  collection,
  orderBy,
  limit
} from 'firebase/firestore';
import indexedDBCache from './indexedDBCache';

import { logger } from "../utils/logger";
export const cacheStats = {
  hits: 0,
  misses: 0,
  savings: 0,
};

class GlobalCacheService {
  constructor() {
    this.cache = new Map();
    this.timestamps = new Map();
    this.subscribers = new Map();
    this.pendingRequests = new Map();

    //  [  v6.0]   - Firestore  95%  
    // /      TTL 
    this.DEFAULT_TTL = 6 * 60 * 60 * 1000; // 6 (26)
    this.USER_TTL = 12 * 60 * 60 * 1000; // 12 (612,    )
    this.ACTIVITY_LOG_TTL = 2 * 60 * 60 * 1000; // 2 (302)
    this.ITEMS_TTL = 24 * 60 * 60 * 1000; // 24 (1224,    )
    this.CLASS_DATA_TTL = 24 * 60 * 60 * 1000; // 24 (1224)
    this.SETTINGS_TTL = 48 * 60 * 60 * 1000; // 48 (2448,    )
    this.GOVERNMENT_SETTINGS_TTL = 48 * 60 * 60 * 1000; // 48 ( )

    //  
    this.MAX_RETRIES = 3;
    this.RETRY_DELAY = 1000; // 1
    this.retryCount = new Map();

    //  [] localStorage   
    this.useLocalStorage = true;
    this.localStoragePrefix = 'gc_'; // globalCache prefix

    //   
    this.startCleanupTimer();
    this.startLocalStorageSync();
  }

  //   
  generateKey(type, params = {}) {
    const sortedParams = Object.keys(params).sort().reduce((result, key) => {
      result[key] = params[key];
      return result;
    }, {});

    return `${type}_${JSON.stringify(sortedParams)}`;
  }

  //  [] IndexedDB      ( )
  get(key) {
    // 1.   
    if (this.cache.has(key)) {
      const expiry = this.timestamps.get(key);
      if (Date.now() <= expiry) {
        cacheStats.hits++;
        cacheStats.savings++;
        return this.cache.get(key);
      }
      this.invalidate(key);
    }

    // 2. localStorage  (IndexedDB    localStorage )
    if (this.useLocalStorage) {
      try {
        const lsKey = this.localStoragePrefix + key;
        const cached = localStorage.getItem(lsKey);
        if (cached) {
          const parsed = JSON.parse(cached);
          const {data, expiry, createdAt} = parsed;

          if (Date.now() <= expiry) {
            //  [] BATCH  5     (   )
            if (key.includes('BATCH') && createdAt) {
              const age = Date.now() - createdAt;
              if (age > 5 * 60 * 1000) { // 5 
                logger.log(`[GlobalCache]   BATCH  : ${key} (${Math.floor(age/1000)} )`);
                localStorage.removeItem(lsKey);
                cacheStats.misses++;
                return null;
              }
            }

            // localStorage    
            this.cache.set(key, data);
            this.timestamps.set(key, expiry);
            logger.log(`[GlobalCache]  localStorage : ${key}`);
            cacheStats.hits++;
            cacheStats.savings++;
            return data;
          } else {
            localStorage.removeItem(lsKey);
          }
        }
      } catch (error) {
        logger.warn('[GlobalCache] localStorage  :', error);
      }
    }

    cacheStats.misses++;
    return null;
  }

  //  [] IndexedDB   get ()
  async getAsync(key) {
    // 1.   
    if (this.cache.has(key)) {
      const expiry = this.timestamps.get(key);
      if (Date.now() <= expiry) {
        cacheStats.hits++;
        cacheStats.savings++;
        return this.cache.get(key);
      }
      this.invalidate(key);
    }

    // 2. IndexedDB  
    try {
      const cachedData = await indexedDBCache.get(key);
      if (cachedData) {
        // IndexedDB    
        this.cache.set(key, cachedData);
        this.timestamps.set(key, Date.now() + this.DEFAULT_TTL);
        logger.log(`[GlobalCache]  IndexedDB : ${key}`);
        cacheStats.hits++;
        cacheStats.savings++;
        return cachedData;
      }
    } catch (error) {
      logger.warn('[GlobalCache] IndexedDB  :', error);
    }

    // 3. localStorage 
    if (this.useLocalStorage) {
      try {
        const lsKey = this.localStoragePrefix + key;
        const cached = localStorage.getItem(lsKey);
        if (cached) {
          const parsed = JSON.parse(cached);
          const {data, expiry, createdAt} = parsed;

          if (Date.now() <= expiry) {
            //  [] BATCH  5    
            if (key.includes('BATCH') && createdAt) {
              const age = Date.now() - createdAt;
              if (age > 5 * 60 * 1000) {
                logger.log(`[GlobalCache]   BATCH  : ${key} (${Math.floor(age/1000)} )`);
                localStorage.removeItem(lsKey);
                cacheStats.misses++;
                return null;
              }
            }

            this.cache.set(key, data);
            this.timestamps.set(key, expiry);
            logger.log(`[GlobalCache]  localStorage : ${key}`);
            cacheStats.hits++;
            cacheStats.savings++;
            return data;
          } else {
            localStorage.removeItem(lsKey);
          }
        }
      } catch (error) {
        logger.warn('[GlobalCache] localStorage  :', error);
      }
    }

    cacheStats.misses++;
    return null;
  }

  //  [] , IndexedDB, localStorage  
  set(key, data, ttl = this.DEFAULT_TTL) {
    const expiry = Date.now() + ttl;
    const createdAt = Date.now(); //  []   

    //   
    this.cache.set(key, data);
    this.timestamps.set(key, expiry);

    // IndexedDB  (,  )
    indexedDBCache.set(key, data, ttl / 1000).catch(err => {
      logger.warn('[GlobalCache] IndexedDB  :', err);
    });

    // localStorage  ()
    if (this.useLocalStorage) {
      try {
        const lsKey = this.localStoragePrefix + key;
        localStorage.setItem(lsKey, JSON.stringify({data, expiry, createdAt})); //  [] createdAt 
      } catch (error) {
        logger.warn('[GlobalCache] localStorage   (  ):', error);
        // localStorage       
        this.cleanupOldestLocalStorageItems(5);
      }
    }

    //  
    if (this.subscribers.has(key)) {
      this.subscribers.get(key).forEach(callback => {
        try {
          callback(data);
        } catch (error) {
          logger.error('  :', error);
        }
      });
    }
  }

  //  [] localStorage      
  cleanupOldestLocalStorageItems(count = 10) {
    try {
      const items = [];
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i);
        if (key && key.startsWith(this.localStoragePrefix)) {
          const cached = localStorage.getItem(key);
          if (cached) {
            const {expiry} = JSON.parse(cached);
            items.push({key, expiry});
          }
        }
      }

      //     
      items.sort((a, b) => a.expiry - b.expiry);

      //    
      for (let i = 0; i < Math.min(count, items.length); i++) {
        localStorage.removeItem(items[i].key);
        logger.log(`[GlobalCache]   : ${items[i].key}`);
      }
    } catch (error) {
      logger.warn('[GlobalCache]   :', error);
    }
  }

  //  []  localStorage  
  invalidate(key) {
    this.cache.delete(key);
    this.timestamps.delete(key);
    this.pendingRequests.delete(key);

    // localStorage 
    if (this.useLocalStorage) {
      try {
        const lsKey = this.localStoragePrefix + key;
        localStorage.removeItem(lsKey);
      } catch (error) {
        // 
      }
    }
  }

  //     ( + localStorage)
  invalidatePattern(pattern) {
    const keysToInvalidate = [];

    //    
    for (const key of this.cache.keys()) {
      if (key.includes(pattern)) {
        keysToInvalidate.push(key);
      }
    }

    //  [] localStorage   
    if (this.useLocalStorage) {
      try {
        for (let i = 0; i < localStorage.length; i++) {
          const lsKey = localStorage.key(i);
          if (lsKey && lsKey.startsWith(this.localStoragePrefix)) {
            const actualKey = lsKey.substring(this.localStoragePrefix.length);
            if (actualKey.includes(pattern) && !keysToInvalidate.includes(actualKey)) {
              keysToInvalidate.push(actualKey);
            }
          }
        }
      } catch (error) {
        logger.warn('[GlobalCache] localStorage   :', error);
      }
    }

    keysToInvalidate.forEach(key => this.invalidate(key));
    logger.log(`[GlobalCache]  '${pattern}' : ${keysToInvalidate.length}  `);
  }

  //  
  subscribe(key, callback) {
    if (!this.subscribers.has(key)) {
      this.subscribers.set(key, new Set());
    }
    this.subscribers.get(key).add(callback);

    //   
    return () => {
      if (this.subscribers.has(key)) {
        this.subscribers.get(key).delete(callback);
        if (this.subscribers.get(key).size === 0) {
          this.subscribers.delete(key);
        }
      }
    };
  }

  //     
  async retryWithBackoff(operation, retryCount = 0) {
    try {
      return await operation();
    } catch (error) {
      // Firestore    
      const isNetworkError =
        error.code === 'unavailable' ||
        error.code === 'deadline-exceeded' ||
        error.message?.includes('Failed to get document') ||
        error.message?.includes('network error') ||
        error.name === 'FirebaseError';

      if (isNetworkError && retryCount < this.MAX_RETRIES) {
        const delay = this.RETRY_DELAY * Math.pow(2, retryCount);
        logger.warn(`[GlobalCache]  ... (${retryCount + 1}/${this.MAX_RETRIES}) - ${delay}ms `);

        await new Promise(resolve => setTimeout(resolve, delay));
        return this.retryWithBackoff(operation, retryCount + 1);
      }

      //       
      if (retryCount >= this.MAX_RETRIES) {
        logger.error('[GlobalCache]    :', error);
      }
      throw error;
    }
  }

  //     
  async executeOrWait(key, operation) {
    //      
    if (this.pendingRequests.has(key)) {
      logger.log('[GlobalCache]     :', key);
      return await this.pendingRequests.get(key);
    }

    //    (  )
    logger.log('[GlobalCache]   :', key);
    const promise = this.retryWithBackoff(operation);
    this.pendingRequests.set(key, promise);

    try {
      const result = await promise;
      logger.log('[GlobalCache]  :', key, ' :', Array.isArray(result) ? `(${result.length})` : typeof result);
      this.pendingRequests.delete(key);
      this.retryCount.delete(key);
      return result;
    } catch (error) {
      logger.error('[GlobalCache] executeOrWait  :', key, error);
      this.pendingRequests.delete(key);
      throw error;
    }
  }

  //    ()
  async getUserDoc(uid, forceRefresh = false) {
    const key = this.generateKey('user', { uid });

    if (!forceRefresh) {
      const cached = this.get(key);
      if (cached) {
        logger.log(`%c[DB]   : users/${uid}`, 'color: #22c55e;');
        return cached;
      }
    }

    return await this.executeOrWait(key, async () => {
      try {
        logger.log(`%c[DB]  Firestore : users/${uid}`, 'color: #f97316; font-weight: bold;');
        const userRef = doc(db, 'users', uid);
        const docSnap = await getDoc(userRef);

        if (docSnap.exists()) {
          const userData = { id: docSnap.id, uid: docSnap.id, ...docSnap.data() };
          this.set(key, userData, this.USER_TTL);
          return userData;
        }
        return null;
      } catch (error) {
        logger.error('[GlobalCache]    :', error);
        //     (  )
        const cached = this.cache.get(key);
        if (cached) {
          logger.warn('[GlobalCache]   -   :', uid);
          return cached;
        }
        throw error;
      }
    });
  }

  async getDoc(collectionPath, docId, forceRefresh = false) {
    const key = this.generateKey(collectionPath, { docId });

    if (!forceRefresh) {
      const cached = this.get(key);
      if (cached) {
        logger.log(`%c[DB]   : ${collectionPath}/${docId}`, 'color: #22c55e;');
        return cached;
      }
    }

    return await this.executeOrWait(key, async () => {
      try {
        logger.log(`%c[DB]  Firestore : ${collectionPath}/${docId}`, 'color: #f97316; font-weight: bold;');
        const docRef = doc(db, collectionPath, docId);
        const docSnap = await getDoc(docRef);

        if (docSnap.exists()) {
          const docData = { id: docSnap.id, ...docSnap.data() };
          this.set(key, docData, this.DEFAULT_TTL);
          return docData;
        }
        return null;
      } catch (error) {
        logger.error(`[GlobalCache] ${collectionPath}/${docId}   :`, error);
        const cached = this.cache.get(key);
        if (cached) {
          return cached;
        }
        throw error;
      }
    });
  }

  //    ()
  async getClassMembers(classCode, forceRefresh = false) {
    const key = this.generateKey('classMembers', { classCode });

    if (!forceRefresh) {
      const cached = this.get(key);
      if (cached) {
        logger.log(`%c[DB]   : classMembers/${classCode} (${cached.length})`, 'color: #22c55e;');
        return cached;
      }
    }

    return await this.executeOrWait(key, async () => {
      try {
        logger.log(`%c[DB]  Firestore  : users (classCode=${classCode})`, 'color: #f97316; font-weight: bold;');
        const usersRef = collection(db, 'users');
        const q = query(usersRef, where('classCode', '==', classCode));
        const querySnapshot = await getDocs(q);

        const members = querySnapshot.docs.map(doc => ({
          id: doc.id,
          uid: doc.id,
          ...doc.data()
        }));

        logger.log(`%c[DB]   : ${members.length} `, 'color: #f97316;');
        this.set(key, members, this.USER_TTL);
        return members;
      } catch (error) {
        logger.error('[GlobalCache]    :', error);
        //    
        const cached = this.cache.get(key);
        if (cached) {
          logger.warn('[GlobalCache]   -   :', classCode);
          return cached;
        }
        throw error;
      }
    });
  }

  //    ()
  async getActivityLogs(classCode, filters = {}, forceRefresh = false) {
    const { lastVisible, ...restFilters } = filters;
    const key = this.generateKey('activityLogs', { classCode, ...restFilters });
    logger.log('[GlobalCache] getActivityLogs :', { classCode, filters, forceRefresh, key });

    if (!forceRefresh && !lastVisible) { // lastVisible    
      const cached = this.get(key);
      if (cached) {
        logger.log('[GlobalCache]    :', cached.logs.length, '');
        return cached;
      }
    }

    logger.log('[GlobalCache] Firestore    :', classCode);
    const operation = async () => {
      try {
        const logsRef = collection(db, 'activity_logs');
        let q = query(logsRef, where('classCode', '==', classCode));

        if (filters.dateFilter && filters.dateFilter !== 'all') {
          const now = new Date();
          let startDate = new Date();
          switch (filters.dateFilter) {
            case 'today': startDate.setHours(0, 0, 0, 0); break;
            case 'week': startDate.setDate(now.getDate() - 7); break;
            case 'month': startDate.setMonth(now.getMonth() - 1); break;
          }
          q = query(q, where('timestamp', '>=', startDate));
        }

        q = query(q, orderBy('timestamp', 'desc'));

        if (lastVisible) {
          q = query(q, startAfter(lastVisible));
        }

        q = query(q, limit(filters.limit || 20)); //  20  

        const querySnapshot = await getDocs(q);
        const logs = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        const newLastVisible = querySnapshot.docs[querySnapshot.docs.length - 1];

        const result = { logs, lastVisible: newLastVisible };

        if (!lastVisible) { //     
            this.set(key, result, this.ACTIVITY_LOG_TTL);
        }

        return result;
      } catch (error) {
        logger.error('[GlobalCache]    :', error);
        const cached = this.cache.get(key);
        if (cached) {
          return cached;
        }
        return { logs: [], lastVisible: null };
      }
    };

    //       (  )
    return await this.retryWithBackoff(operation);
  }

  //    ()
  async getItems(forceRefresh = false) {
    const key = this.generateKey('items', {});

    if (!forceRefresh) {
      const cached = this.get(key);
      if (cached) {
        logger.log('[GlobalCache]  getItems -   (Firestore  0):', cached?.length, '');
        return cached;
      }
    }

    return await this.executeOrWait(key, async () => {
      try {
        logger.log('[GlobalCache]  getItems - Firestore   (19  )');
        const itemsRef = collection(db, 'storeItems');
        const querySnapshot = await getDocs(itemsRef);

        const items = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));

        logger.log('[GlobalCache]  getItems - Firestore   (' + items.length + ' )');
        this.set(key, items, this.ITEMS_TTL);
        return items;
      } catch (error) {
        logger.error('[GlobalCache]  getItems -  :', error.message);
        const cached = this.cache.get(key);
        if (cached) {
          logger.warn('[GlobalCache]    -    (Firestore  0)');
          return cached.data;
        }
        throw error;
      }
    });
  }

  //    ()
  async getUserItems(userId, forceRefresh = false) {
    const key = this.generateKey('userItems', { userId });

    if (!forceRefresh) {
      const cached = this.get(key);
      if (cached) {
        logger.log('[GlobalCache]  getUserItems -   (Firestore  0):', cached?.length, '');
        return cached;
      }
    }

    return await this.executeOrWait(key, async () => {
      try {
        logger.log('[GlobalCache]  getUserItems - Firestore   (' + userId + '/inventory)');
        const userInventoryRef = collection(db, 'users', userId, 'inventory');
        const querySnapshot = await getDocs(userInventoryRef);

        const userItems = querySnapshot.docs.map(doc => ({
          id: doc.id,
          source: 'inventory',
          ...doc.data()
        }));

        logger.log('[GlobalCache]  getUserItems - Firestore   (' + userItems.length + ' )');
        this.set(key, userItems, this.ITEMS_TTL);
        return userItems;
      } catch (error) {
        logger.error('[GlobalCache]  getUserItems -  :', error.message);
        const cached = this.cache.get(key);
        if (cached) {
          logger.warn('[GlobalCache]    -    (Firestore  0)');
          return cached.data;
        }
        throw error;
      }
    });
  }

  //  [] localStorage   
  startLocalStorageSync() {
    // 30 localStorage   
    setInterval(() => {
      if (!this.useLocalStorage) return;

      try {
        const now = Date.now();
        const keysToDelete = [];

        for (let i = 0; i < localStorage.length; i++) {
          const key = localStorage.key(i);
          if (key && key.startsWith(this.localStoragePrefix)) {
            const cached = localStorage.getItem(key);
            if (cached) {
              const {expiry} = JSON.parse(cached);
              if (now > expiry) {
                keysToDelete.push(key);
              }
            }
          }
        }

        keysToDelete.forEach(key => localStorage.removeItem(key));

        if (keysToDelete.length > 0) {
          logger.log(`[GlobalCache] localStorage : ${keysToDelete.length}   `);
        }
      } catch (error) {
        logger.warn('[GlobalCache] localStorage  :', error);
      }
    }, 30 * 60 * 1000); // 30
  }

  //   
  startCleanupTimer() {
    setInterval(() => {
      const now = Date.now();
      const expiredKeys = [];

      for (const [key, expiry] of this.timestamps.entries()) {
        if (now > expiry) {
          expiredKeys.push(key);
        }
      }

      expiredKeys.forEach(key => this.invalidate(key));

      if (expiredKeys.length > 0) {
        logger.log(`[GlobalCache]   : ${expiredKeys.length}   `);
      }
    }, 60000); // 1 
  }

  //     
  clearUserData(userId) {
    this.invalidatePattern(`user_${userId}`);
    this.invalidatePattern(`userItems_${userId}`);
  }

  //     
  clearClassData(classCode) {
    this.invalidatePattern(`classMembers_${classCode}`);
    this.invalidatePattern(`activityLogs_${classCode}`);
  }

  //    (Memory + IndexedDB + localStorage)
  async clearAll() {
    this.cache.clear();
    this.timestamps.clear();
    this.subscribers.clear();
    this.pendingRequests.clear();

    // IndexedDB 
    try {
      if (window.indexedDB) {
        const databases = await window.indexedDB.databases?.() || [];
        for (const db of databases) {
          if (db.name?.startsWith('alchan-cache')) {
            window.indexedDB.deleteDatabase(db.name);
          }
        }
      }
    } catch (e) { /* IndexedDB    */ }

    // localStorage   
    try {
      const keysToRemove = [];
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i);
        if (key?.startsWith('cache_') || key?.startsWith('alchan_')) {
          keysToRemove.push(key);
        }
      }
      keysToRemove.forEach(key => localStorage.removeItem(key));
    } catch (e) { /* localStorage    */ }
  }

  //   
  getStats() {
    return {
      cacheSize: this.cache.size,
      pendingRequests: this.pendingRequests.size,
      subscribers: this.subscribers.size,
      memoryUsage: JSON.stringify([...this.cache.entries()]).length
    };
  }
}

//   
export const globalCache = new GlobalCacheService();

// React Hook
export const useGlobalCache = () => {
  return globalCache;
};

export default globalCache;