// src/firebase/firebaseConfig.js - Firebase    

import { initializeApp } from "firebase/app";
import {
  getFirestore,
  connectFirestoreEmulator,
} from "firebase/firestore";
import {
  getAuth,
  connectAuthEmulator,
} from "firebase/auth";
import { getStorage } from "firebase/storage";
import { getFunctions, connectFunctionsEmulator } from "firebase/functions";

import { logger } from "../utils/logger";
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.REACT_APP_FIREBASE_DATABASE_URL,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID
};

logger.log("[firebase.js] Firebase   ...");
const app = initializeApp(firebaseConfig);

const db = getFirestore(app);
logger.log("[firebase.js] Firestore   ( )");

const auth = getAuth(app);
const storage = getStorage(app);
const functions = getFunctions(app, 'asia-northeast3');
logger.log("[firebase.js] Firebase   ");

if (process.env.NODE_ENV === 'development') {
  logger.log('[firebase.js]   :  ...');
  try {
    connectFirestoreEmulator(db, '127.0.0.1', 8080);
    connectAuthEmulator(auth, 'http://127.0.0.1:9099');
    connectFunctionsEmulator(functions, '127.0.0.1', 5001);
    logger.log('[firebase.js]    .');
  } catch (error) {
    logger.error('[firebase.js]   :', error);
  }
}

const isInitialized = () => {
  const initialized = Boolean(app && db && auth);
  if (!initialized) {
    logger.warn(
      "[firebase.js] Firebase    .",
      { app: !!app, db: !!db, auth: !!auth }
    );
  }
  return initialized;
};

export { app, db, auth, storage, functions, isInitialized };
